<template>
    <div class="type">
        <van-nav-bar class="header" title="分类" left-arrow style="height: 55px;border-bottom: 2px solid skyblue;">
        <template #right>
            <van-icon class="serch" name="search"/>
        </template>
        </van-nav-bar>

        <van-tabs v-model="act" swipeable class="tabs" sticky offset-top="57px">
        <van-tab v-for="(index,i) in title" :title="index" :name="active[i]">
            <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
                >
                <div class="goods">
                    <div class="card"  v-for="(i,index) in data">
                        <img :src="JSON.parse(i.images)[0]"/>
                        <p>{{i.name}}</p>
                        <p>{{i.price}}</p>
                    </div>
                </div>
            </van-pull-refresh>
        </van-tab>
        </van-tabs>
    </div>
</template>
<script>
import { Toast } from 'vant';
    export default{
        data(){
            return{
                data:[],
                title:['书籍','电子产品','xxx','vvv'],
                active: ['book','digital','c','d'],
                act:'book',
                isLoading: false,
            }
        },
        methods:{
            onClick(name){
                this.act=name
                this.getdata(name)
            },
            onRefresh() {
                setTimeout(() => {
                    this.isLoading = false;
                    Toast('刷新成功');
                }, 1000);
            },
            getdata(val){
            this.$api.gettype({type:val}).then(res=>{
                this.data=res.data.data
            })
            }
        },
        mounted(){
            this.getdata(this.act)
        },
        watch:{
            act:{
                handler(newval,oldval){
                    this.onClick(newval)
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .header{
        position: sticky;
        top: 0;
        .serch{
            font-size: 20px;
        }
    }
    .goods{
        display: flex;
        flex-wrap: wrap;
    }
    .card{
        --n:2;
        // --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
        --gap: calc((100% - 184px * var(--n)) / var(--n) /2);
        width: 184px;
        height: 255px;
        background: rgb(255, 255, 255);
        margin: 2px var(--gap);
        // margin: 10px; 
        img{
            width: 100%;height: 210px;object-fit: cover;
        }
    }
</style>
